.portfolio-item {
	position: relative;
	padding: 30px 20px 20px;
	color: $white;
	@include sm {
		color: $text; }
	@include hover {
		.portfolio-item__image {
			transform: scale(1.04);
			&:before {
				opacity: 0.9; } }
		.portfolio-item__descr {
			@include fadein; } }
	&__image-wrapper {
		@include coverdiv;
		overflow: hidden;
		@include sm {
			position: relative;
			padding-bottom: 50%; } }
	&__image {
		@include coverdiv;
		@include cover;
		transition: transform $trLong;
		backface-visibility: hidden;
		&:before {
			content: '';
			@include coverdiv;
			// background: #36d1dc
			background: linear-gradient(to left, #36d1dc, #5b86e5);
			// background-image: linear-gradient( 135deg, #F6D242 10%, #FF52E5 100%)
			opacity: 0.4;
			transition: opacity $trLong; } }
	&__content {
		position: relative;
		z-index: 5;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: space-between;
		min-height: 280px;
		@include sm {
			min-height: 0; } }
	.link-btn {
		&:after {
			content: '';
			@include coverdiv; } }
	&__title {
		margin-bottom: 16px; }
	&__descr {
		font-size: 12px;
		max-width: 190px;
		margin-bottom: 12px;
		color: $white;
		@include fadeout($trLong);
		@include sm {
			@include fadein;
			color: $text; }
		p {
			&:not(:last-child) {
				margin-bottom: 6px; } } } }

.portfolio {
	display: flex;
	flex-wrap: wrap;
	// margin: 0 -15px
	&__item {
		padding-bottom: 30px;
		// +flex-size(50%)
 } }		// padding: 0 15px 30px
