html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

@font-face { font-family: "Lora"; font-style: normal; font-weight: 700; src: local("Lora-Bold"), local("Lora-Bold"), url("../fonts/Lora-Bold.woff2") format("woff2"), url("../fonts/Lora-Bold.woff") format("woff"); }

@font-face { font-family: "Roboto"; font-style: normal; font-weight: 400; src: local("Roboto-Regular"), local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

.icon-arrow-right { width: 1em; height: 1em; fill: currentColor; }

.icon-facebook { width: 0.53em; height: 1em; fill: currentColor; }

body { background: #FFF; line-height: 1.4; font-family: "Roboto"; font-size: 14px; font-weight: 400; }

.title { font-family: "Lora", "Times New Roman", Times, serif; font-weight: 700; font-size: 24px; }

.inner { margin: 0 auto; padding: 0 30px; max-width: 1280px; }

.inner-sm { margin: 0 auto; padding: 0 20px; max-width: 1000px; }

.row { margin: 0px -15px; display: flex; flex-wrap: wrap; }

[class*='col'] { padding: 0 15px; }

.col-1 { flex: 0 0 8.33333%; min-width: 8.33333%; }

@media only screen and (max-width: 767px) { .col-sm-1 { flex: 0 0 8.33333%; min-width: 8.33333%; } }

.col-2 { flex: 0 0 16.66667%; min-width: 16.66667%; }

@media only screen and (max-width: 767px) { .col-sm-2 { flex: 0 0 16.66667%; min-width: 16.66667%; } }

.col-3 { flex: 0 0 25%; min-width: 25%; }

@media only screen and (max-width: 767px) { .col-sm-3 { flex: 0 0 25%; min-width: 25%; } }

.col-4 { flex: 0 0 33.33333%; min-width: 33.33333%; }

@media only screen and (max-width: 767px) { .col-sm-4 { flex: 0 0 33.33333%; min-width: 33.33333%; } }

.col-5 { flex: 0 0 41.66667%; min-width: 41.66667%; }

@media only screen and (max-width: 767px) { .col-sm-5 { flex: 0 0 41.66667%; min-width: 41.66667%; } }

.col-6 { flex: 0 0 50%; min-width: 50%; }

@media only screen and (max-width: 767px) { .col-sm-6 { flex: 0 0 50%; min-width: 50%; } }

.col-7 { flex: 0 0 58.33333%; min-width: 58.33333%; }

@media only screen and (max-width: 767px) { .col-sm-7 { flex: 0 0 58.33333%; min-width: 58.33333%; } }

.col-8 { flex: 0 0 66.66667%; min-width: 66.66667%; }

@media only screen and (max-width: 767px) { .col-sm-8 { flex: 0 0 66.66667%; min-width: 66.66667%; } }

.col-9 { flex: 0 0 75%; min-width: 75%; }

@media only screen and (max-width: 767px) { .col-sm-9 { flex: 0 0 75%; min-width: 75%; } }

.col-10 { flex: 0 0 83.33333%; min-width: 83.33333%; }

@media only screen and (max-width: 767px) { .col-sm-10 { flex: 0 0 83.33333%; min-width: 83.33333%; } }

.col-11 { flex: 0 0 91.66667%; min-width: 91.66667%; }

@media only screen and (max-width: 767px) { .col-sm-11 { flex: 0 0 91.66667%; min-width: 91.66667%; } }

.col-12 { flex: 0 0 100%; min-width: 100%; }

@media only screen and (max-width: 767px) { .col-sm-12 { flex: 0 0 100%; min-width: 100%; } }

.block { display: flex; }

.link-btn { display: flex; align-items: center; background: none; font-family: "Roboto"; color: #fff; letter-spacing: 0.05em; text-transform: uppercase; text-decoration: none; font-size: 0; }

@media only screen and (max-width: 767px) { .link-btn { height: 30px; } }

.no-touch .link-btn:hover .link-btn__icon { transform: translateX(3px); }

.link-btn__text { font-size: 12px; }

.link-btn__icon { position: relative; margin-left: 5px; transition: transform .3s; }

.link-btn__icon .icon { font-size: 12px; }

.portfolio-item { position: relative; padding: 30px 20px 20px; color: #fff; }

@media only screen and (max-width: 767px) { .portfolio-item { color: #4a484a; } }

.no-touch .portfolio-item:hover .portfolio-item__image { transform: scale(1.04); }

.no-touch .portfolio-item:hover .portfolio-item__image:before { opacity: 0.9; }

.no-touch .portfolio-item:hover .portfolio-item__descr { opacity: 1; visibility: visible; }

.portfolio-item__image-wrapper { position: absolute; top: 0; left: 0; width: 100%; height: 100%; overflow: hidden; }

@media only screen and (max-width: 767px) { .portfolio-item__image-wrapper { position: relative; padding-bottom: 50%; } }

.portfolio-item__image { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; transition: transform 0.6s; backface-visibility: hidden; }

.portfolio-item__image:before { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: linear-gradient(to left, #36d1dc, #5b86e5); opacity: 0.4; transition: opacity 0.6s; }

.portfolio-item__content { position: relative; z-index: 5; display: flex; flex-direction: column; align-items: flex-start; justify-content: space-between; min-height: 280px; }

@media only screen and (max-width: 767px) { .portfolio-item__content { min-height: 0; } }

.portfolio-item .link-btn:after { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.portfolio-item__title { margin-bottom: 16px; }

.portfolio-item__descr { font-size: 12px; max-width: 190px; margin-bottom: 12px; color: #fff; opacity: 0; visibility: hidden; transition: opacity 0.6s, visibility 0.6s; }

@media only screen and (max-width: 767px) { .portfolio-item__descr { opacity: 1; visibility: visible; color: #4a484a; } }

.portfolio-item__descr p:not(:last-child) { margin-bottom: 6px; }

.portfolio { display: flex; flex-wrap: wrap; }

.portfolio__item { padding-bottom: 30px; }
